import './index.html';
import './index.css';
import heroCard from './HeroCard';

const changeHero = (target) => {
  if (target.classList.contains('hero-card')) {
    const currentHero = document.querySelector('.hero-card-checked');
    const choosenHero = target;

    currentHero.classList.remove('hero-card-checked');
    choosenHero.classList.add('hero-card-checked');
  }
};

window.onload = () => {
  heroCard.showHeroCards();
  const heroes = document.querySelector('.hero-cards-wrapper');
  const playButton = document.querySelector('.login-button-play');
  const nameInput = document.querySelector('input[name="user-name"]');

  heroes.firstElementChild.nextElementSibling.classList.add('hero-card-checked');
  heroes.addEventListener('click', (event) => { changeHero(event.target.parentElement); });
  heroes.addEventListener('keyup', (event) => {
    switch (event.code) {
      case 'Enter':
        changeHero(event.target);
        break;
      default:
    }
  });

  playButton.addEventListener('click', (event) => {
    const name = encodeURIComponent(nameInput.value);
    const type = encodeURIComponent(document.querySelector('.hero-card-checked').lastElementChild.textContent);

    if (name && nameInput.value.match(/^\s+$/) === null) {
      playButton.href += `?name=${name}&type=${type}`;
    } else {
      event.preventDefault();
      nameInput.focus();
    }
  });
};
