import { heroes, heroImagesPath } from './heroes';

class HeroCard {
  static showHeroCards() {
    const fragment = document.createDocumentFragment();
    const cardWrapper = document.querySelector('.hero-cards-wrapper');
    const template = document.querySelector('.hero-card-temp');

    heroes.forEach((hero) => {
      const cardClone = template.content.cloneNode(true);
      const cardImg = cardClone.querySelector('.hero-card img');
      const cardCaption = cardClone.querySelector('.hero-card figcaption');

      cardImg.setAttribute('src', `${heroImagesPath}/${hero}-card.jpg`);
      cardCaption.textContent = hero;

      fragment.appendChild(cardClone);
    });

    cardWrapper.appendChild(fragment);
  }
}

export default HeroCard;
